import React from "react";
import { Link } from "react-router-dom";
import {
  Panel,
  PanelHeader,
  PanelBody,
} from "./../../components/panel/panel.jsx";
import { Line } from "react-chartjs-2";
import { getToday } from "../../components/helpers/function.jsx";
import PerfectScrollbar from 'react-perfect-scrollbar'
import 'react-perfect-scrollbar/dist/css/styles.css';
import { reduxForm } from 'redux-form';
import { connect } from 'react-redux';
import { getDataPenjualanPerbulan, getPembelianToday, getPenjualanToday, getPesananToday, getServicesToday } from "../../actions/datamaster_action.jsx";
import { getDataPembelianPerbulan } from './../../actions/datamaster_action';
const maptostate = (state) => {
  return {
    datapenjualanbulanan : state.datamaster.getDataPenjualanPerbulan,
    datapembelianbulanan : state.datamaster.getDataPembelianPerbulan,
    penjualan_hari_ini : state.datamaster.getPenjualanToday || 0,
    pembelian_hari_ini : state.datamaster.getPembelianToday || 0,
    pesanan_hari_ini : state.datamaster.getPesananToday || 0,
    service_hari_ini : state.datamaster.getServicesToday || 0,
  }
}
class DashboardV1 extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
    };

   

    this.lineChartOptions = {
      maintainAspectRatio: false,
      scales: {
        yAxes: [
          {
            ticks: {
              beginAtZero: true,
              fontColor: "black",
            },
          },
        ],
        xAxes: [
          {
            ticks: {
              fontColor: "black",
            },
          },
        ],
      },
      legend: {
        labels: {
          fontColor: "black",
        },
      },
    };
  }

  componentDidMount() {
    this.props.dispatch(getDataPembelianPerbulan())
    this.props.dispatch(getDataPenjualanPerbulan())
    this.props.dispatch(getPenjualanToday())
    this.props.dispatch(getPembelianToday())
    this.props.dispatch(getPesananToday())
    this.props.dispatch(getServicesToday())

   
  }
  
  render() {
    const lineChartData = {
      labels: [
        "JANUARI",
        "FEBRUARI",
        "MARET",
        "APRIL",
        "MEI",
        "JUNI",
        "JULI",
        "AGUSTUS",
        "SEPTEMBER",
        "OKTOBER",
        "NOVEMBER",
        "DESEMBER",
      ],
      datasets: [
        {
          label: "Penjualan",
          fill: false,
          lineTension: 0.1,
          backgroundColor: "rgba(0, 172, 172, 0.25)",
          borderColor: "#FF5B57",
          borderWidth: 2,
          pointBorderColor: "#FF5B57",
          pointBackgroundColor: "#fff",
          pointBorderWidth: 2,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: "#fff",
          pointHoverBorderColor: "#FF5B57",
          pointHoverBorderWidth: 3,
          pointRadius: 3,
          pointHitRadius: 10,
          data: this.props.datapenjualanbulanan,
        },
        {
          label: "Pembelian",
          fill: false,
          lineTension: 0.1,
          backgroundColor: "rgba(52, 143, 226, 0.25)",
          borderColor: "#F59C1A",
          borderWidth: 2,
          pointBorderColor: "#F59C1A",
          pointBackgroundColor: "#fff",
          pointBorderWidth: 2,
          pointHoverRadius: 5,
          pointHoverBackgroundColor: "#fff",
          pointHoverBorderColor: "#F59C1A",
          pointHoverBorderWidth: 3,
          pointRadius: 3,
          pointHitRadius: 10,
          data: this.props.datapembelianbulanan,
        },
      ],
    };
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Home</Link>
          </li>
          <li className="breadcrumb-item active">Dashboard</li>
        </ol>
        <h1 className="page-header">Dashboard</h1>
        <div className="row">
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-red">
              <div className="stats-icon">
                <i className="fa fa-shopping-cart"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PENJUALAN HARI INI</h4>
                <p>{this.props.penjualan_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-penjualan">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-orange">
              <div className="stats-icon">
                <i className="fa fa-shopping-bag"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PEMBELIAN HARI INI</h4>
                <p>{this.props.pembelian_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-pembelian">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-grey-darker">
              <div className="stats-icon">
                <i className="fa fa-boxes"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL PESANAN HARI INI</h4>
                <p>{this.props.pesanan_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-pesanan">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
          <div className="col-xl-3 col-md-6">
            <div className="widget widget-stats bg-black-lighter">
              <div className="stats-icon">
                <i className="fa fa-tools"></i>
              </div>
              <div className="stats-info">
                <h4>TOTAL SERVICE HARI INI</h4>
                <p>{this.props.service_hari_ini}</p>
              </div>
              <div className="stats-link">
                <Link to="/lihat-services">
                  Lihat Detail <i className="fa fa-arrow-alt-circle-right"></i>
                </Link>
              </div>
            </div>
          </div>
        </div>
        <div className="row">
          <div className="col-xl-9">
            <Panel>
              <PanelHeader>Analisa Transaksi / Bulan</PanelHeader>
              <PanelBody>
                <Line
                  data={lineChartData}
                  height={300}
                  options={this.lineChartOptions}
                />
              </PanelBody>
            </Panel>
          </div>
          <div className="col-xl-3">
            <Panel>
              <PanelHeader>Release Note</PanelHeader>
              <PanelBody>
                <div className="text-success" style={{height: "300px", overflowY: "auto"}}>
                <PerfectScrollbar>
                  <ul>
                    <li>
                      Peningkatan Performa {getToday()}
                    </li>
                  </ul>
                </PerfectScrollbar>
                </div>

              </PanelBody>
            </Panel>
          </div>
        </div>
      </div>
    );
  }
}

DashboardV1 = reduxForm({
  form: "FormDashboard",
  enableReinitialize: true,
})(DashboardV1);
export default connect(maptostate, null)(DashboardV1);
